/**
 * Editor Styles
 */

.formello-plugin-templates-modal {

	&.formello-plugin-templates-modal-loading {

		.formello-control-tabs {
			pointer-events: none;
			opacity: 0.3;
		}

		.formello-plugin-templates-modal-loading-spinner {
			position: absolute;
			top: 14px;
			right: 50px;
			z-index: 10;

			.components-spinner {
				float: none;
				margin-left: 0;
			}
		}
	}

	.formello-plugin-templates-categories-row {
		margin-top: 20px;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
	}

	.formello-plugin-templates-categories-select {
		max-width: 170px;
		margin-bottom: 10px;
	}

	.formello-plugin-templates-count {
		max-width: 170px;
		margin-bottom: 20px;
		opacity: 0.7;
	}
}

.formello-plugin-templates-spinner .components-spinner {
	float: none;
	margin-left: 0;
}

.formello-plugin-templates-list {
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	list-style: none;
	margin-top: -15px;
	margin-left: -15px;

	> .formello-plugin-templates-list-item {
		flex: 0 0 33.333%;
		width: 33.333%;
		padding-top: 15px;
		padding-left: 15px;

		@media screen and (max-width: 840px) {
			flex: 0 0 50%;
			width: 50%;
		}

		@media screen and (max-width: 450px) {
			flex: 0 0 100%;
			width: 100%;
		}

		> a {
			display: block;
			width: 100%;
			cursor: pointer;
			padding: 10px;
			text-align: center;
			background-color: #fff;
			border: 1px solid #ddd;
			border-radius: 4px;
			box-shadow: 0 0 0 2px #fff;
			transition: 0.2s border-color, 0.2s box-shadow;

			&:hover,
			&:focus,
			&:active {
				box-shadow: 0 0 0 2px #216eb7;
			}

			.formello-plugin-templates-list-item-image {
				position: relative;
				display: block;

				img {
					display: block;
					width: 100%;
					height: auto;
					border-top-left-radius: 4px;
					border-top-right-radius: 4px;
				}

				.formello-plugin-templates-list-item-image-sizer {
					width: 100%;

					+ .lazyload-wrapper img {
						position: absolute;
						top: 0;
						left: 0;
						height: 100%;
					}
				}
			}
		}

		.formello-plugin-templates-list-item-title {
			padding: 5px;
			font-size: 14px;
			font-weight: 600;
		}
	}
}

.formello-plugin-templates-list-item-no-thumb .formello-plugin-templates-list-item-title {
	border-top: 0 !important;
}

.formello-component-modal .components-modal__header {
	margin-bottom: 0;
}

.formello-component-modal-tab-panel {

	.components-tab-panel__tab-content {
		min-height: 300px;
	}
}

.formello-plugin-templates-modal {
	min-width: 600px;
	width: 60%;
}

.wp-block-formello-form {
	min-height: 100px;
	padding: 20px;
}

div.formello p {
	margin-bottom: 0 !important;
}

.formello-row {
	flex-direction: row;
}

.formello-urlinput {

	input {
		border: 1px solid rgb(117, 117, 117) !important;
		max-width: 100%;
	}
}
